@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__oc {
  margin: 60px 14px 0;
}
.astudio__oc-block1 h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 40px;
  line-height: 49.44px;
  color: #000000;
  margin: 0 0 17px 0;
}
.astudio__oc p {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-family: 18px;
  line-height: 28px;
  color: #757575;
}
.astudio__oc-block2 .ourclient-images {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.astudio__oc-block2 .ourclient-images .ourclient-img {
  margin-right: 20px;
  margin-top: 10px;
}
@media (min-width: 1025px) {
  .astudio__oc-block2 .ourclient-images .ourclient-img {
    margin-right: 60px;
    margin-top: 0;
  }
}
.astudio__oc-block2 .ourclient-images .ourclient-img img {
  width: 100%;
  min-width: 90px;
  max-width: 149px;
  height: 100%;
}
.astudio__oc-block2 .ourclient-images .ourclient-img:last-child img {
  margin-top: 21px;
}
@media (min-width: 1025px) {
  .astudio__oc-block2 .ourclient-images .ourclient-img:last-child img {
    margin-top: 0;
  }
}
@media (min-width: 1025px) {
  .astudio__oc-block2 .ourclient-images .ourclient-img:nth-child(4) {
    margin-top: 18px;
    margin-right: 0;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__oc-block2 .ourclient-images {
    justify-content: space-around;
    align-items: center;
  }
}
@media (min-width: 1025px) {
  .astudio__oc-block2 .ourclient-images {
    flex-wrap: nowrap;
  }
}
@media (min-width: 1025px) {
  .astudio__oc-block2 {
    margin-left: 60px;
    padding-top: 30px;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__oc {
    display: flex;
    align-items: center;
  }
}
@media (min-width: 1025px) {
  .astudio__oc {
    display: grid;
    grid-template-columns: 324px 1fr;
    align-items: center;
    margin: 136px 120px 0 120px;
  }
  .astudio__oc h2 {
    font-family: "Averta Demo PE Cutted Demo";
    font-size: 40px;
    line-height: 49.44px;
    color: #000000;
    margin: 0 0 17px 0;
  }
  .astudio__oc p {
    font-family: "Averta Demo PE Cutted Demo";
    font-weight: 400;
    font-family: 18px;
    line-height: 28px;
    color: #757575;
    margin: 0 0 17px 0;
  }
}